import { Component } from '@angular/core';
import { IonicPage, MenuController, NavController, Platform } from 'ionic-angular';

import { TranslateService } from '@ngx-translate/core';

export interface Slide {
  title: string;
  description: string;
  image: string;
  title2: string;
  description2: string;
  image2: string;

}

@IonicPage()
@Component({
  selector: 'page-tutorial',
  templateUrl: 'tutorial.html'
})
export class TutorialPage {
  slides: Slide[];
  showSkip = true;
  dir: string = 'ltr';

  constructor(public navCtrl: NavController,
              public menu: MenuController,
              public platform: Platform) {
    this.dir = platform.dir();


        this.slides = [
          {
            title:"BCC Faith Ministries",
            description: "Welcome to Borrowdale Community Church Mobile App",
            image: 'assets/img/appicon.png',
            title2:"",
            description2:"",
            image2:"",

          },
          {
            title: "Devotions",
            description: "Members will get powerful word delivered directly to them",
            image: 'assets/img/devotions.png',

            title2:"Church Service Notifications",
            description2:"Members will be reminded of all upcoming services and events",
            image2:"assets/img/ica-slidebox-img-3.png",
          },
          {
            title: "Prayer Network",

            description: "Members can engage with each other in prayer network anytime\n",
            image: 'assets/img/ica-slidebox-img-1.png',
            title2:"Giving And Fundraisings",
            description2:"Members can easily give towards tithes offerings or projects from the mobile app.\n",
            image2:"assets/img/ica-slidebox-img-2.png"
          }




        ];

  }

  startApp() {
    this.navCtrl.setRoot('HomePage', {}, {
      animate: true,
      direction: 'forward',

    });
  }

  onSlideChangeStart(slider) {
    this.showSkip = !slider.isEnd();
  }

  ionViewDidEnter() {
    // the root left menu should be disabled on the tutorial page
    this.menu.enable(false);
  }

  ionViewWillLeave() {
    // enable the root left menu when leaving the tutorial page
    this.menu.enable(true);
  }

}



